@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap);
.i{
    background-color: #e2e2e2;
    display: flex;
    height: 90vh;
}

.i-left{
    flex:1 1;
    align-items: center;
    justify-content: center;

}
.i-right{
    flex:1 1;
    position: relative;
}
.i-left-wrapper{
    padding-left: 230px;
    padding-top: 250px;
    height: 100%;
    display: flex;
    flex-direction: column;
    
    
}
.i-intro{
    font-size: 40px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
    margin-top: 40px;
}
.i-title-item{
    height: 60px;
    font-size: 45px;
    font-weight: bold;
    color: #59b256;
    
    display: flex;
    align-items: center;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    50%{
        transform: translateY(-64px);
    }
    100%{
        transform: translateY(-130px);
    }
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.i-bg{
    -webkit-clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    /* clip-path: polygon(22% 29%, 100% 0, 100% 100%, 0% 100%); */
    /* clip-path: circle(47.4% at 50% 50%); */
    /* clip-path: polygon(0 0, 100% 0%, 100% 100%, 46% 100%); */


    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.i-desc{
    margin-top: 40px;
}

/* Responsive */

@media screen and (max-width:479px){
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
        padding: 40px;
        height: 100%;
        align-items: center;
        text-align: center;
        justify-content: center;
        flex-direction: column;
        
    }

    .i-intro{
        font-size: 30px;
        font-weight: 300;
    }
    
    .i-name{
        font-size: 38px;
    }

    .i-title-item{
        height: 60px;
        font-size: 35px;
        font-weight: bold;
        
    }
    .i-right{
        margin-top: 0px;
    }

}


