    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 520px) {
  .main-nav {
    height: 6rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .logo h2 {
    font-size: 2rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  /* hero section  */

  .hero-section h1 {
    font-size: 3.8rem;
  }




}


.hamburger {
  position: relative;
  display: block;
  width: 40px;
  cursor: pointer;
  
  -webkit-appearance: none;
  
          appearance: none;
  background: none;
  outline: none;
  border: none;
  z-index: 99;
}

.hamburger .bar, .hamburger:after, .hamburger:before {
  content: '';
  display: block;
  width: 100%;
  height: 4px;
  background-color: rgb(0, 0, 0);
  margin: 6px 0px;
  transition: 0.4s;
}

.hamburger.is-active:before {
  background-color: white;
  transform: rotate(-45deg) translate(-8px, 6px);
}

.hamburger.is-active:after {
  background-color: white;
  transform: rotate(45deg) translate(-8px, -6px);
}

.hamburger.is-active .bar {
  opacity: 0;
}


.mobile-nav {
  /* top: 0; */

  text-transform: uppercase;
  font-size: 20px;
  position: fixed;
  top: 0px;
  left: 100%;
  width: 100%;

  min-height: 100vh;
  display: block;
  z-index: 98;
  background-color: #1f103F;
  padding-top: 100px;
  transition: 0.4s;
}

.mobile-nav.is-active {
  left: 0;
}

.mobile-nav a {
  
  display: block;
  width: 100%;
  max-width: 200px;
  margin: 0 auto 16px;
  text-align: center;
  padding: 12px 16px;
  background-color: #24104f;
  border-radius: 25px;

  color: #FFF;
  text-decoration: none;
}

.mobile-nav a:hover {
  -webkit-appearance: none;
          appearance: none;
  background: none;
  background-color:  #331f5e;
  outline: none;
  border: none;
}

nav .mobile-nav{
  display: block;
  
}


@media screen and (min-width: 840px) {
  .mobile-nav {
		display: none;
	}

	.hamburger {
		display: none;
	}
}
  

